

import axios from 'axios'
import weui from 'weui.js'

import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import DatetimeRange from 'vux/src/components/datetime-range/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import value2name from 'vux/src/filters/value2name.js'


/* form */
// 约定正则
var regexp = {
    regexp: {
    }
};

const GLOBAL_VAR_DOMAIN = "http://localhost:8080/"
const IMAGE_SERVER_URL = "http://localhost:8080/ImageServer/fileUpload"
const GLOBAL_VAR_SUFFIX = "03a68667-05dc-11e6-af3b-00163e021542/100000/e7afdd55291a3e3796a6d3d83e70d6dd"
const V2 = 'footballer/api/mobile/v2'
const CREATE_EVENT_API = GLOBAL_VAR_DOMAIN + V2 + '/tournament-service/createTournament/' + GLOBAL_VAR_SUFFIX

export default {
    mounted : function() {
        const that = this
        const hiddenElements = document.getElementsByClassName('hidden')

        function getQueryVariable(variable) {
           var query = window.location.search.substring(1);
           var vars = query.split("&");
           for (var i=0;i<vars.length;i++) {
                   var pair = vars[i].split("=");
                   if(pair[0] == variable){return pair[1];}
           }
           return(false);
        }

        // validation
        weui.form.checkIfBlur('#form')
        
        // add event listener to trigger weui validation when onblur
        Array.prototype.forEach.call(hiddenElements, function(element) {
            element.addEventListener('validate', function() {
                element.dispatchEvent(new Event('focus'))
            })
        })

        // 这里是简单的调用，其余api请参考文档
        weui.uploader('#uploaderCustom', {
            url: IMAGE_SERVER_URL,
            auto: false,
            onQueued: function() {
                that.$data.uploadCustomFileList.push(this);
            }
        });

        // get parameter
        const tournamentId = getQueryVariable('tournamentId')
        if (tournamentId) {
            this.submiLabel = '更新'
            let loading = weui.loading('加载...');
            const GET_TOURNAMENT_API = GLOBAL_VAR_DOMAIN + V2 + '/tournament-service/getTournamentBaseInfo/' + tournamentId + '/' + GLOBAL_VAR_SUFFIX
            axios.get(GET_TOURNAMENT_API, {tournamentId : tournamentId})
              .then(function(response) {
                loading.hide()
                console.log(response)
                weui.toast('加载成功', 3000)

                const tournamentBaseInfo = response.data.tournamentBaseInfo

                that.id = tournamentId
                that.name = tournamentBaseInfo.name
                that.mainOrganiserName = tournamentBaseInfo.mainOrganiserName
                that.matchType = tournamentBaseInfo.matchType
                that.address = tournamentBaseInfo.addressId.split(',')
                that.matchStartDate = tournamentBaseInfo.startDt.split(':')
                that.matchEndDate = tournamentBaseInfo.endDt.split(':')
                that.registerStartDate = tournamentBaseInfo.registerStartDate.split(':')
                that.registerEndDate = tournamentBaseInfo.registerStartDate.split(':')
              })
              .catch(function(error) {
                loading.hide()
                console.log('error')
                weui.toast('加载失败', 3000)
              })
        }
    },
    components : {
        ChinaAddressV3Data,
        XAddress,
        DatetimeRange,
        Divider, 
        Checker, 
        CheckerItem
    },
    data() {
        return {
            id : null,
            name : '',
            address : [],
            addressData: ChinaAddressV3Data,
            registerStartDate : [],
            registerEndDate : [],
            matchStartDate : [],
            matchEndDate : [],
            matchType: '',
            uploadCustomFileList : [],
            mainOrganiserName : '',
            submiLabel : '保存'
        }
    },
    
    methods: {
        currentStartDate : function() {
            const date = new Date()
            const year = date.getFullYear()
            const month = parseInt(date.getMonth()) + 1
            const displayMonth = month + 1 < 10 ? '0' + month : month
            const day = date.getDate()
            const dayDisplay = day < 10 ? '0' + day : day  
            const result = year + '-' + displayMonth + '-' + dayDisplay

            return result
        },
        currentEndDate : function() {
            const date = new Date()
            const year = parseInt(date.getFullYear()) + 1
            return year + '-12-31'
        },


        reigsterEndBeginDate : function() {
            let result
            result = this.registerStartDate.length > 0 ? 
                            this.registerStartDate[0] : this.currentStartDate()
            return result
        },

        matchStartBeginDate : function() {
            let result
            result = this.registerStartDate.length > 0 ? 
                            this.registerStartDate[0] : this.currentStartDate()
            return result
        },


        matchEndBeginDate : function() {
            let result
            result = this.matchStartDate.length > 0 ? 
                            this.matchStartDate[0] : this.currentStartDate()
            return result
        },
        
        getName : function(address) {
            const value = value2name(this.address, this.addressData)
            return value
        }, 
        fireValidate : function(value) {
            const hiddenElements = document.getElementsByClassName('hidden')
            Array.prototype.forEach.call(hiddenElements, function(element) {
                element.dispatchEvent(new Event('validate'))
            })
        },
        toDatString : function(dateArray) {
            if (dateArray.length > 2) {
                return dateArray[0]
            }
        },
        submit : function() {
            const that = this
            let model = {}
            weui.form.validate('#form', function (error) {
            console.log(error);
            if (!error) {
                let loading = weui.loading('提交中...');

                // upload logo
                that.uploadLogo()

                model.id = that.id
                model.name = that.$data.name
                model.address = that.getName(that.$data.address)
                model.addressId = that.$data.address.join()
                model.startDt = that.$data.matchStartDate.join(':')
                model.endDt = that.$data.matchEndDate.join(':')
                model.registerStartDate = that.$data.registerStartDate.join(':')
                model.registerEndDate = that.$data.registerEndDate.join(':')
                model.matchType = that.$data.matchType
                model.mainOrganiserName = that.$data.mainOrganiserName

                console.log('data:', that.$data)
                console.log('model:', model)

                axios.post(CREATE_EVENT_API, model)
                  .then(function() {
                    loading.hide()
                    weui.toast('提交成功', 3000)
                    location.href = GLOBAL_VAR_DOMAIN + 'footballer/mobile/match/football/matchListDashboard.html'
                  })
                  .catch(function(error) {
                    loading.hide()
                    weui.toast('提交失败', 200)
                  })
            }
            
            }, regexp);
        },
        uploadLogo : function() {
            this.$data.uploadCustomFileList.forEach(function(file){
                file.upload()
            })
        },
        preview : function(e) {
            const target = e.target
            const uploadCustomFileList = this.$data.uploadCustomFileList

            while(!target.classList.contains('weui-uploader__file') && target){
                target = target.parentNode;
            }
            if(!target) return;

            let url = target.getAttribute('style') || '';
            const id = target.getAttribute('data-id');

            if(url){
                url = url.match(/url\((.*?)\)/)[1];
            }
            const gallery = weui.gallery(url, {
                onDelete: function(){
                    weui.confirm('确定删除该图片？', function(){
                        var index;
                        for (var i = 0, len = uploadCustomFileList.length; i < len; ++i) {
                            var file = uploadCustomFileList[i];
                            if(file.id == id){
                                index = i;
                                break;
                            }
                        }
                        if(index) uploadCustomFileList.splice(index, 1);

                        target.remove();
                        gallery.hide();
                    });
                }
            });
        }
    }
}




