// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import vuexI18n from 'vuex-i18n'
import App from './App'
//import Home from './components/Event'
//import Home from './components/Wechat'
import Home from './components/PublishEvent/Index'

import 'weui/src/style/weui.less'

import VeeValidate from 'vee-validate'


Vue.use(VueRouter)
Vue.use(Vuex)
Vue.use(VeeValidate)
//Vue.use(axios)


const GLOBAL_VAR_DOMAIN="http://localhost:8080/"
const GLOBAL_VAR_SUFFIX="03a68667-05dc-11e6-af3b-00163e021542/100000/e7afdd55291a3e3796a6d3d83e70d6dd"


const store = new Vuex.Store()
Vue.use(vuexI18n.plugin, store)

console.log('import vuex i18n')

const routes = [{
  path: '/',
  component: Home
}]

const router = new VueRouter({
  routes
})

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  router,
  render: h => h(App)
}).$mount('#app-box')



